/*Orietançaõ dos buttons dentro das divs*/

.logoJs{
    width: 150px;
    height: 150px;
    font-size: 80px;
    color: white;
    background-color: #090113f3;
    border: none;
    border: 1px solid black;
}/*teste*/
.logoRc{
    width: 150px;
    height: 150px;
    font-size: 80px;
    color: white;
    background-color: #090113f3;
    border: none;
    border: 1px solid black;
}
.logoHtml{
    width: 150px;
    height: 150px;
    font-size: 80px;
    color: white;
    background-color: #090113f3;
    border: none;
    border: 1px solid black;
}
.logoCss{
    width: 150px;
    height: 150px;
    font-size: 80px;
    color: white;
    background-color: #090113f3;
    border: none;
    border: 1px solid black;
}
/*Animação dos buttons*/
.logoJs:hover, .logoJs:focus{
    color: black;
    outline: 0;
    background-color: rgb(243, 207, 2);
    transition-duration: 0.2s;
    transition-delay: 0.2s;
    border: 1px solid white;
}
.logoRc:hover, .logoRc:focus{
    background-color: rgb(82, 102, 212);
    transition-duration: 0.2s;
    transition-delay: 0.2s;
    color: black;
    outline: 0;
    border: 1px solid white;
}
.logoHtml:hover, .logoHtml:focus{
    background-color: rgb(167, 94, 11);
    transition-duration: 0.2s;
    transition-delay: 0.2s;
    color: black;
    outline: 0;
    border: 1px solid white;
}
.logoCss:hover, .logoCss:focus{
    background-color: rgb(21, 28, 138);
    color: black;
    outline: 0;
    transition-duration: 0.2s;
    transition-delay: 0.2s;
    border: 1px solid white;
}

/*Edição das imagens*/
.projetos{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
}
.imagemCheck,  .imagemForm{
    background-color: black;
    border-radius: 20px;
    border: 2px solid black;
    width: 250px;
}
.imagemCep{ 
    background-color: black;
    border-radius: 20px;
    border: 2px solid black;
    width: 230px;
    height: 190px;
}
.imagemForm{
    background-color: black;
    border-radius: 20px;
    border: 2px solid black;
    width: 230px;
    height: 190px;
}
.projetoCheck,.projetoCep, .projetoForm{
    padding: 30px;
    text-align: center;
    border: 1px solid white;
    margin: 20px;
    height: 500px;
    width: 400px;
    border-radius: 20px;
    background-color:  rgb(25, 25, 31);
}

/*Tratamento da imagem ao passar o mouse*/
.imagemCheck:hover, .imagemCheck:focus, .imagemCep:hover, .imagemCep:focus, .imagemForm:hover,.imagemForm:focus  {
    opacity: 60%;
    border: 2px solid rgb(170, 17, 190); 
}
